.app__navbar {
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 1rem 2rem;
    background: rgba(235, 235, 235, 0.33);
    //0.25 makes the navbar feel like it's above current background
    backdrop-filter: blur(4px);
    -webkit-backdrop-filter: blur(4px);
    // bc not available on all browsers
    border: 1px solid rgba (255, 255, 255, 0.18);
    position: fixed;
    z-index: 2;
    //makes it appear above the content
}

.app__navbar-logo {
    display: flex;
    justify-content: flex-start;
    align-items: center;

    //targets image inside app__navbar-logo
    img {
        width: 100px;
        height: 25px;

        @media screen and (min-width: 2000px) {
            width: 180px;
            height: 40px;
        }
        //for extra large devices
    }
}

.app__navbar-links {
    flex: 1;
    display: flex;
    justify-content: center;
    align-items: center;

    list-style: none;
    li {
        margin: 0 1rem;
        cursor: pointer;
        flex-direction: column;

        div {
            width: 5px;
            height: 5px;
            background: transparent;
            border-radius: 50%;
            margin-bottom: 5px;
            // small dot that appears when you hover
        }

        a {
            color: var(--gray-color);
            text-decoration: none;
            flex-direction: column;
            font-weight: 500;
            transition: all 0.3s ease-in-out;
            
            &:hover {
                //& means we are referring to the element we are inside of
                color: var(--secondary-color);
            }
        }
        &:hover {
            div {
                background: var(--secondary-color);
            }
        }
    }

    @media screen and (max-width: 900px) {
        display: none;
    }
}

.app__navbar-menu {
    width: 35px;
    height: 35px;
    border-radius: 50%;
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    background: var(--secondary-color);
    svg {
        width: 70%;
        height: 70%;
        color: var(--white-color);
    }
    div {
        position: fixed;
        top: 0;
        bottom: 0;
        right: 0;
        z-index: 5;
        padding: 1rem;
        width: 80%;
        height: 100vh;
        display: flex;
        justify-content: flex-end;
        align-items: flex-end;
        flex-direction: column;
        background: url('../../assets/bgWhite.png');
        background-color: var(--primary-color);
        background-size: cover;
        background-repeat: repeat;
        box-shadow: 0 0 20px rgba(168, 168, 168, 0.15);

        svg {
            width: 35px;
            height: 35px;
            color: var(--secondary-color);
            margin: 0.5rem 1rem;
        }

        ul {
            list-style: none;
            margin: 0;
            padding: 0;
            height: 100%;
            width: 100%;
            display: flex;
            justify-content: flex-start;
            align-content: flex-start;
            flex-direction: column;

            li {
                margin: 1rem;
            
                a {
                    color: var(--gray-color);
                    text-decoration: none;
                    font-size: 1rem;
                    font-weight: 500;

                    transition: all 0.3s ease-in-out;
                    &:hover {
                        color: var(--secondary-color);
                    }  
                }
            }
        }

        @media screen and (min-width: 900px) {
            display: none;
        }
    }

    @media screen and (min-width: 900px) {
        display: none;
    }
}
